export const __metadata = { PACKAGE_NAME: '@wix/online-programs' };

export function createInstructor(
  httpClient: HttpClient
): CreateInstructorSignature {
  return (
    options?: NonNullablePaths<CreateInstructorOptions, `instructor.name`, 3>
  ) =>
    universalCreateInstructor(
      options,
      // @ts-ignore
      { httpClient }
    );
}

interface CreateInstructorSignature {
  /**
   * Creates a new instructor
   * @returns The created instructor
   */
  (
    options?: NonNullablePaths<CreateInstructorOptions, `instructor.name`, 3>
  ): Promise<
    NonNullablePaths<
      Instructor,
      | `photo._id`
      | `photo.url`
      | `photo.height`
      | `photo.width`
      | `role`
      | `programIds`,
      3
    >
  >;
}

