export const onInstructorCreated = EventDefinition(
  'wix.achievements.instructors.v2.instructor_created',
  true,
  (event: InstructorCreatedEnvelope) =>
    renameKeysFromRESTResponseToSDKResponse(
      transformPaths(event, [
        {
          transformFn: transformRESTTimestampToSDKTimestamp,
          paths: [
            { path: 'entity.createdDate' },
            { path: 'metadata.eventTime' },
          ],
        },
      ])
    )
)<InstructorCreatedEnvelope>();
