function resolveWixAchievementsInstructorsV2InstructorsServiceUrl(
  opts: Omit<ResolveUrlOpts, 'domainToMappings'>
) {
  const domainToMappings = {
    '*.dev.wix-code.com': [
      {
        srcPath: '/_api/instructors-service',
        destPath: '',
      },
    ],
    'editor.wixapps.net': [
      {
        srcPath: '/_api/instructors-service',
        destPath: '',
      },
    ],
    'manage._base_domain_': [
      {
        srcPath: '/_api/instructors-service',
        destPath: '',
      },
    ],
    'www._base_domain_': [
      {
        srcPath: '/_api/instructors-service',
        destPath: '',
      },
    ],
    _: [
      {
        srcPath: '/_api/instructors-service',
        destPath: '',
      },
    ],
    'www.wixapis.com': [
      {
        srcPath: '/instructors-service',
        destPath: '',
      },
      {
        srcPath: '/_api/instructors-service',
        destPath: '',
      },
    ],
    'editor._base_domain_': [
      {
        srcPath: '/_api/instructors-service',
        destPath: '',
      },
    ],
    'blocks._base_domain_': [
      {
        srcPath: '/_api/instructors-service',
        destPath: '',
      },
    ],
    'create.editorx': [
      {
        srcPath: '/_api/instructors-service',
        destPath: '',
      },
    ],
  };

  return resolveUrl(Object.assign(opts, { domainToMappings }));
}

const PACKAGE_NAME = '@wix/auto_sdk_online-programs_instructors';

/** Creates a new instructor */
export function createInstructor(payload: object): RequestOptionsFactory<any> {
  function __createInstructor({ host }: any) {
    const serializedData = transformPaths(payload, [
      {
        transformFn: transformSDKTimestampToRESTTimestamp,
        paths: [{ path: 'instructor.createdDate' }],
      },
    ]);
    const metadata = {
      entityFqdn: 'wix.achievements.instructors.v2.instructor',
      method: 'POST' as any,
      methodFqn:
        'wix.achievements.instructors.v2.InstructorsService.CreateInstructor',
      packageName: PACKAGE_NAME,
      migrationOptions: {
        optInTransformResponse: true,
      },
      url: resolveWixAchievementsInstructorsV2InstructorsServiceUrl({
        protoPath: '/v2/instructors',
        data: serializedData,
        host,
      }),
      data: serializedData,
      transformResponse: (payload: any) =>
        transformPaths(payload, [
          {
            transformFn: transformRESTTimestampToSDKTimestamp,
            paths: [{ path: 'instructor.createdDate' }],
          },
        ]),
    };

    return metadata;
  }

  return __createInstructor;
}

