/** Instructor entity */
export interface Instructor {
  /**
   * ID of the instructor that equals to memberId
   * @format GUID
   * @readonly
   */
  _id?: string | null;
  /**
   * User ID for real instructors (will be filled only for real instructors)
   * @format GUID
   * @readonly
   */
  userId?: string | null;
  /**
   * The name of the instructor
   * @maxLength 1000
   */
  name?: string | null;
  /**
   * A description of the instructor
   * @maxLength 10000
   */
  description?: string | null;
  /** URL of profile photo */
  photo?: Image;
  /**
   * Alt text for profile photo (needed for a11y)
   * @maxLength 1000
   */
  photoAltText?: string | null;
  /**
   * Slug that determines the instructor's profile page URL.
   * @format URL_SLUG
   * @readonly
   */
  slug?: string | null;
  /**
   * Role of the instructor
   * @readonly
   */
  role?: RoleWithLiterals;
  /**
   * IDs of programs instructor is assigned to
   * @format GUID
   * @readonly
   * @maxSize 100
   */
  programIds?: string[];
  /**
   * Date and time when the instructor was created
   * @readonly
   */
  _createdDate?: Date | null;
}

export interface Image {
  /**
   * Wix Media image ID,
   * set when the member selects an image from Wix Media.
   */
  _id?: string;
  /** Image URL. */
  url?: string;
  /** Original image width. */
  height?: number;
  /** Original image height. */
  width?: number;
  /**
   * X-axis offset.
   *
   * Default: `0`.
   */
  offsetX?: number | null;
  /**
   * Y-axis offset.
   *
   * Default: `0`.
   */
  offsetY?: number | null;
}

export enum Role {
  /** VIRTUAL instructor */
  DEFAULT = 'DEFAULT',
  /** REAL instructor who is the owner of the site */
  OWNER = 'OWNER',
  /** REAL instructor who has access to specified list of programs */
  INSTRUCTOR = 'INSTRUCTOR',
  /** REAL instructor who has the same rights as program owner */
  MANAGER = 'MANAGER',
}

/** @enumType */
export type RoleWithLiterals =
  | Role
  | 'DEFAULT'
  | 'OWNER'
  | 'INSTRUCTOR'
  | 'MANAGER';

